gsap.to(".preloader", 1.75, { 
    delay: 3.5, 
    y: "-100%",
    ease: "power4.out"
});
gsap.to(".box-color", 1.3, { 
    delay: 4.3, 
    y: "-100%",
    ease: "power4.out"
});

gsap.to(".preloader__txt", 1.2, { 
    opacity: 0,
    delay: 2.7, 
    // y: "100%" 
});




gsap.from(".h1", 1.5, { 
    delay: 4.1, 
    opacity: 0,
    y: "100%",
    ease: "slow(0.7, 0.7, false)",
});

gsap.from(".li", 2.1, { 
    delay: 4.4, 
    opacity: 0,
    y: "40%",
    ease: "slow(0.7, 0.7, false)",
});

