    // const t7 = gsap.timeline({ paused: true });


    t0.fromTo(
      item0,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 3.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

